  updateChart(data, symbol) {

    if (symbol.includes('CE')) {
      this.chartOptions1.title = {
        text: symbol
      }
      this.chartOptions1.series[0] = {
        type: 'candlestick',
        id: 'base',
        name: symbol,
        /* dataGrouping: {
          approximation: 'ohlc'
        }, */
        data: data,
        /* dataGrouping: {
          forced: true,
          //approximation: 'ohlc',
          units: [
            ['minute', [5]]
          ]
        } */
      };
  
      this.updateFlag1 = true;
    } else if (symbol.includes('PE')) {
      this.chartOptions2.title = {
        text: symbol
      }
      this.chartOptions2.series[0] = {
        type: 'candlestick',
        id: 'base',
        name: symbol,
        /* dataGrouping: {
          approximation: 'ohlc'
        }, */
        data: data,
        /* dataGrouping: {
          forced: true,
          //approximation: 'ohlc',
          units: [
            ['minute', [5]]
          ]
        } */
      };
  
      this.updateFlag2 = true;
    }
  }
  
