  showOILimit(instru, scrip) {
    
    let strike = scrip;
    let type = '';
    if (strike.includes('CE')) {
      type = 'CE';
    }
    if (strike.includes('PE')) {
      type = 'PE';
    }
    strike = Number(strike.slice(0, -2)); // remove CE PE at end

    if (instru === 'NIFTY' && AppConstants.nfCEOILimit && AppConstants.nfPEOILimit) {
      if (type === 'CE') {
        return strike > AppConstants.nfCEOILimit;
      }
      if (type === 'PE') {
        return strike < AppConstants.nfPEOILimit;
      }
    }

    if (instru === 'BANKNIFTY' && AppConstants.bnfCEOILimit && AppConstants.bnfPEOILimit) {
      if (type === 'CE') {
        return strike > AppConstants.bnfCEOILimit;
      }
      if (type === 'PE') {
        return strike < AppConstants.bnfPEOILimit;
      }
    }

    return false;
  }

  nudgeOrder(instru, instru_ltp) {

    let spotStrike = (Math.round(instru_ltp / 100) * 100) + 1000;
    let scrip = spotStrike + 'CE';

    // let scrip = "39000CE";

    //let symbol = "BANKNIFTY22JUL38000CE"; //this.getMapKeyString(this.instru, this.currExpiry, scrip);
    let symbol = this.getMapKeyString(instru, this.currExpiry, scrip);
    console.log('nudge order for ', symbol);

    // response:
    // {"status":"success","data":[[{"type":"bnf_range","message":"Use MIS (Intraday) to trade all option strikes with no position conversion (MIS to NRML). This will act as a risk management check, ensuring you dont hold any losing intraday positions overnight. NRML buy orders are blocked for this strike price due to [open interest (OI)](https://support.zerodha.com/category/trading-and-markets/kite-web-and-mobile/articles/why-did-my-bank-nifty-option-order-get-rejected) limits prescribed by SEBI. You can exit existing positions or place NRML buy orders between 35900 - 36200.","description":"Use MIS (Intraday) to trade all option strikes with no position conversion (MIS to NRML). This will act as a risk management check, ensuring you dont hold any losing intraday positions overnight. NRML buy orders are blocked for this strike price due to [open interest (OI)](https://support.zerodha.com/category/trading-and-markets/kite-web-and-mobile/articles/why-did-my-bank-nifty-option-order-get-rejected) limits prescribed by SEBI. You can exit existing positions or place NRML buy orders between 35900 - 36200.","severity":"low","prompt":false}]]}

    this.zerodhaService.nudge_order(symbol, this.orders_authorization).subscribe((res: any) => {
      console.log('nudgeOrder response', res, res.data[0][0]);

      if (res.data[0] && res.data[0][0] && res.data[0][0].message) {
        let msg = res.data[0][0].message;
        let sliced = msg.slice(-14);
        sliced = sliced.slice(0, -1);
        let split = sliced.split('-');
        if (instru === 'NIFTY') {
          AppConstants.nfPEOILimit = Number(split[0].trim());
          AppConstants.nfCEOILimit = Number(split[1].trim());
        }
        if (instru === 'BANKNIFTY') {
          AppConstants.bnfPEOILimit = Number(split[0].trim());
          AppConstants.bnfCEOILimit = Number(split[1].trim());
        }
  
        // console.log(ceOILimit, peOILimit);
        // this.appService.requestStatusEvent$.next({'status': 'success', 'message': 'Nudge Success ' + scrip});
      } else {
        AppConstants.nfPEOILimit = null;
        AppConstants.nfCEOILimit = null;
        AppConstants.bnfPEOILimit = null;
        AppConstants.bnfCEOILimit = null;
      }
    },
    error => {
      console.log('nudgeOrder error', error);
      let errmsg = 'Error (mostly timeout)';
      if (error.error && error.error.message) {
        errmsg = error.error.message;
      } else {
        errmsg = error.message;
      }
      // this.appService.requestStatusEvent$.next({'status': 'danger', 'message': scrip + ' ' + errmsg});
      // this.appService.playAudio('error');
    });
  }

